
import { defineComponent, ref, Ref } from 'vue'

export default defineComponent({
  name: 'EpicInput',
  props: {
    modelValue: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      default: '',
    },
  },
  setup(_, { emit }) {
    const inputEl: Ref<HTMLElement | null> = ref(null)

    function updateValue(value: string) {
      emit('update:modelValue', value)
    }

    function focus() {
      if (inputEl.value != null) {
        inputEl.value.focus()
      }
    }

    return {
      inputEl,
      updateValue,
      focus,
    }
  },
})
